var cdb = (typeof window !== "undefined" ? window['cdb'] : typeof global !== "undefined" ? global['cdb'] : null);

module.exports = cdb.core.Model.extend({

  defaults: {
    msg: '',
    display: false
  },

  shouldDisplay: function () {
    return this.get('display') && !!this.get('msg');
  },

  show: function (str) {
    return this.set({
      display: true,
      msg: str
    });
  },

  hide: function () {
    this.set('display', false);
  }

});
